import React from 'react';
import Navlink from '../components/Navlink.js'

export default class Multiple extends React.Component {

	componentDidMount() {
		document.title = 'Nalezeno vce adres'
	}

	render() {
		return (
			<div className="main">
		      <p className="text focusable" tabIndex="-1">Nael jsem vce adres, vyber jednu z nich prosm.</p>
		      <form action="">
		        <Navlink value="Dlouh 3859/12" to="#/usegps"/>
						<Navlink value="Jungmannova 680/25" to="#/usegps"/>
						<Navlink value="Karlovo nmst 1623/6" to="#/usegps"/>
						<Navlink value="Zpt na hledn" to="#/"/>
		      </form>
		  </div>
		);
	}
}



