import React from 'react';
import Navlink from '../components/Navlink.js'

export default class Lost extends React.Component {

	componentDidMount() {
		document.title ='Oven polohy'
	}

	render() {
		return (
			<div className="main">
				<p className="text focusable" tabIndex="-1">Pravdpodobn jsi mimo pvodn trasu. Upesnm tvou lokaci a najdu novou trasu.</p>
				<form action="">
					<Navlink value="Dobe" to="#/walk"/>
					<Navlink value="Zpt do navigace" to="#/segment"/>
				</form>
		  </div>
		);
	}
}



