import React from 'react';
import Navlink from '../components/Navlink.js'

export default class Reportother extends React.Component {

	componentDidMount() {
		document.title = 'Nahlen neblokujcho problmu na trase'
	}

	render() {
		return (
			<div className="main">
				<p className="text focusable" tabIndex="-1">Jak problm se tedy na trase objevil?</p>
	      <form action="">
	        {/* <select name="kind" id="kind" aria-label="Vyberte prosm typ problmu" required>
						<option value="">Typ problmu</option>
	          <option value="inappropriate">Cesta je nevhodn</option>
	          <option value="badinfo">Informace o trase nesed</option>
	          <option value="other">Jin</option>
	        </select> */}
					<div className="radios">
						<p className="text">Vyberte prosm typ problmu</p>
						<input type="radio" name="kind" id="kind1" aria-labelledby="kind1label" value="Cesta je nevhodn" required/>
						<label htmlFor="kind1" id="kind1label" aria-hidden="true">Cesta je nevhodn</label>
						<input type="radio" name="kind" id="kind2" aria-labelledby="kind2label" value="Informace o trase nesed" required/>
						<label htmlFor="kind2" id="kind2label" aria-hidden="true">Informace o trase nesed</label>
						<input type="radio" name="kind" id="kind3" aria-labelledby="kind3label" value="Jin" required/>
						<label htmlFor="kind3" id="kind3label" aria-hidden="true">Jin</label>
					</div>

	        <label htmlFor="description" id="descriptionlabel" className="inputlabel" aria-hidden="true">Krtk popis (nepovinn):</label>
	        <textarea name="description" id="description" aria-labelledby="descriptionlabel" rows="4" cols="80" className="inputside"></textarea>
					<Navlink value="Nahlsit" to="#/thankssegment"/>
	        <Navlink value="Zpt do navigace" to="#/segment"/>
	      </form>
		  </div>
		);
	}
}



