import React from 'react';
import Navlink from '../components/Navlink.js'

export default class Walk extends React.Component {

	componentDidMount() {
		document.title = 'Zpesnn GPS'
	}

	render() {
		return (
			<div className="main">
		      <p className="text focusable" tabIndex="-1">Aby se zvila pesnost GPS, potebuji aby jsi uel pr metr. eknu ti kdy pestat.</p>
					<p className="text">Poznmka: Tlatko "dal krok" se objev pouze po upesnn GPS lokace.</p>
		      <form action="">
						<Navlink value="Dal krok" to="#/roadside"/>
		        <Navlink value="Zpt na hledn" to="#/"/>
		      </form>
		  </div>
		);
	}
}



