import React from 'react';
import Navlink from '../components/Navlink.js'

export default class Startnavigation extends React.Component {

	componentDidMount() {
		document.title = 'Trasa nalezena'
	}

	render() {
		return (
			<div className="main">
				<p className="text focusable" tabIndex="-1">Trasa z adresy Myslkova 1134/9 na Karlovo nmst 1623/6. Trasa je piblin 1100 metr dlouh a vede pes 5 pechod. Postav se tak, e bude mt vozovku po sv prav stran.</p>
				<form action="">
					<Navlink value="Zat navigaci" to="#/segment"/>
					<Navlink value="Zpt na hledn" to="#/"/>
				</form>
		  </div>
		);
	}
}



