import React from 'react';
import Navlink from '../components/Navlink.js'

export default class Crowdsourcing extends React.Component {

	componentDidMount() {
		document.title = 'Dodaten sbr dat o trase'
	}

	render() {
		return (
			<div className="main">
		      <p className="text focusable" tabIndex="-1">Doplujc otzka: Byl ten roh, kolem kterho jsem t te navigoval spe ostr nebo zaoblen?</p>
		      <form action="">
		        <Navlink value="Ostr" to="#/thankssegment"/>
		        <Navlink value="Zaoblen" to="#/thankssegment"/>
		        <Navlink value="Nevm" to="#/thankssegment"/>
		        <Navlink value="Zpt na hledn" to="#/"/>
		      </form>
		  </div>
		);
	}
}



