import React from 'react';
import Navlink from '../components/Navlink.js'

export default class Roadside extends React.Component {

	componentDidMount() {
		document.title = 'Po kter ruce je vozovka'
	}

	render() {
		return (
			<div className="main">
				<p className="text focusable" tabIndex="-1">U to bude, jen jet potebuji vdt po kter ruce m momentln vozovku.</p>
				<form action="">
					<Navlink value="Vlevo" to="#/startnavigation"/>
					<Navlink value="Vpravo" to="#/startnavigation"/>
					<Navlink value="dn vozovka tu nen" to="#/startnavigation"/>
					<Navlink value="Zpt na hledn" to="#/"/>
				</form>
		  </div>
		);
	}
}



