import React from 'react';
import Navlink from '../components/Navlink.js'

export default class Segment extends React.Component {

	componentDidMount() {
		document.title = '1. st trasy z celkovch 21 st'
	}

	render() {
		return (
			<div className="main">
				<p className="text focusable" tabIndex="-1">1. st trasy z celkovch 21 st. Jsi te na adrese Myslkova 1134/9. Jdi asi 70 metr rovn mrn z kopce k zaoblenmu rohu s ulic Chberova.</p>
				<form action="">
					<Navlink value="Dal st" to="#/crowdsourcing"/>
					<Navlink value="Pedchoz st" to="#/prevsegment"/>
					<Navlink value="Zkontrolovat polohu" to="#/lost"/>
					<Navlink value="Nahlsit problm na trase" to="#/report"/>
					<Navlink value="Zpt na hledn" to="#/"/>
				</form>
		  </div>
		);
	}
}



